@import url(https://fonts.googleapis.com/css2?family=Karla&display=swap);
:root {
  --primary: rgba(0, 0, 0, 0.5);
  --secondary: #fa439e;
  --tertiary: #03e9f4;
  --light: #ffffff;
  --dark: #777777;
  --error: #03e9f4;
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

