/* /////////////////////////////////////////////CURTAIN ANIMATION */
a:before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  /* width: 100vw;
  height: 100vh; */
  background-color: rgb(0, 0, 0, 0.2);
  overflow: hidden;
  z-index: 2;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: all 0.5s;
  /* filter: blur(8px); */
}

a:hover:before {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

a:hover {
  text-decoration: none;
  color: #ffffff;
  color: var(--light);
}

button {
  color: #fa439e;
  color: var(--secondary);
  font-style: oblique;
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  /* border-radius: .5rem; */
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: rgba(0, 0, 0, 0);
  /* color: #03e9f4; */
  padding: 10px 20px;
  text-decoration: none;
  text-transform: uppercase;
  /* letter-spacing: 4px; */
  text-align: center;
  transition: all 0.4s;
  cursor: pointer;
  /* line-height: 50px; */
  /* transition:.3s all; */
}

button:hover {
  opacity: 0.8;
  color: white;
  overflow: hidden;
  transition: 0.5s;
  background: rgba(0, 0, 0, 0.5);
  background: var(--primary);
  overflow: hidden;
  /* filter: blur(1px); */
}

button[disabled] {
  cursor: default;
  opacity: 0.8;
}

/* img {
  max-width: 100%;
} */

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid #777777;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid #fa439e;
  border: 1px solid var(--secondary);
  box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

.container {
  width: 85%;
  max-width: 1200px;
