/* ///////////////////////////////////////////// NAV   */
li {
  color: #03e9f4;
  padding: 10px 20px;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 4px;
  text-align: center;
  transition: all 0.4s;
  cursor: pointer;
  line-height: 50px;
}

.nav,
li:hover {
  overflow: hidden;
  transition: 0.5s;
  background: rgba(0, 0, 0, 0.5);
  background: var(--primary);
  color: #fa439e;
  color: var(--secondary);
}

.nav {
  width: 100%;
  height: 50px;
  margin-top: 0;
  background-color: rgba(0, 0, 0, 0);
  color: #fefefe;
  z-index: 3;
}

ul {
  list-style-type: none;
  padding: 0;
}
/* //////////////////////////////////////////////////// */

.flex-row {
  display: flex;
  flex-wrap: wrap;
  /* justify-content: space-evenly; */
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: calc(1rem * 0.5);
  margin-right: var(--spacing-one);
  margin-left: calc(1rem * 0.5);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: calc(1rem);
  margin-right: var(--spacing-two);
  margin-left: calc(1rem);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: calc(1rem * 0.5);
  margin-top: var(--spacing-one);
  margin-bottom: calc(1rem * 0.5);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: calc(1rem);
  margin-top: var(--spacing-two);
  margin-bottom: calc(1rem);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: calc(1rem * 0.5);
  padding-right: var(--spacing-one);
  padding-left: calc(1rem * 0.5);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: calc(1rem);
  padding-right: var(--spacing-two);
  padding-left: calc(1rem);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: calc(1rem * 0.5);
  padding-top: var(--spacing-one);
  padding-bottom: calc(1rem * 0.5);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: calc(1rem);
  padding-top: var(--spacing-two);
  padding-bottom: calc(1rem);
  padding-bottom: var(--spacing-two);
}

.card {
  height: 400px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 40px 63px -60px #777777;
  box-shadow: 0 40px 63px -60px var(--dark);
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 25%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0);
}
.card-img {
  width: 55vw;
  height: 30vh;
  /* background-color: var(--dark); */
  background: center center no-repeat;
  /* opacity: 60%; */
  cursor: pointer; /* Add a pointer on hover */
  /* overflow: hidden; */
  justify-content: space-around;
  align-items: center;
}
.card-img:hover {
  width: 100vw;
  height: 50vh;
  -webkit-transform: scale(1.45);
          transform: scale(1.45);
  overflow: hidden;
  opacity: 60%;
  transition-duration: 0.5s;
  justify-content: inherit;
  -webkit-filter: blur(0.5px);
          filter: blur(0.5px);
}
.error-text {
  color: #03e9f4;
  color: var(--error);
}

