.cart {
  position: fixed;
  top: 0;
  right: 0;
  min-width: 20%;
  max-width: 30%;
  max-height: 60%;
  background-color: var(--light);
  overflow: auto;
  padding: .5rem;
  box-shadow: 0 0 1rem rgba(0, 0, 0, .5);
  border-bottom-left-radius: .5rem;
 
}

.cart h2 {
  font-size: 1.5rem;
  border-bottom: 1px solid var(--dark);
  padding-bottom: .5rem;
  margin: 1rem 0;
}

.cart .close {
  position: absolute;
  top: .5rem;
  right: .5rem;
  cursor: pointer;
  font-size: 1.5em;
  color: var(--tertiary);
}

.cart .close:hover {
  text-decoration: underline;
}

.cart-closed {
  /* background-image: "./images/LightMode_Mouse.png"; */
  position: fixed;
  top: 2%;
  right: 2%;
  /* font-size: 2rem; */
  cursor: pointer;
  /* background-color: var(--secondary); */
  /* border-radius: 50%; */
  /* padding: .25rem;
  width: 50px;
  height: 50px; */
}

.cart-closed:hover {
  -webkit-transform: rotate(8deg);
          transform: rotate(8deg);
  -webkit-transform: scale(1.25);
          transform: scale(1.25);
}

.cart img {
  width: 70px;
  margin-right: 1rem;
}

.cart input {
  width: 50px;
  padding: 0;
  margin: 0 .5rem 1rem 0;
}

@media screen and (max-width: 768px) {
  .cart {
    position: relative;
    max-width: 100%;
    max-height: auto;
    box-shadow: none;
    background-color: inherit;
  }

  .cart-closed {
    position: relative;
    margin: 0 auto;
  }
}
body {
  font-family: Karla;
}

.content {
  max-width: 1280px;
  overflow-x: hidden;
  margin: 0 auto;
}

.art {
  height: 100%;
  display: flex;
  flex-direction: column;
  max-width: 24rem;
  position: relative;
  border: 1px solid #ccc;
}

.art-header {
  display: flex;
  height: 20px;
  justify-content: space-between;
  align-items: center;
}

.art-header span {
  color: #2d3748;
}

.artImageHolder {
  display: flex;
  width: 100%;
  align-items: center;
}

.art-image {
  width: 100%;
  height: 100%;
  z-index: 0;
  display: flex;
  align-items: center;
}

.art button {
  display: inline-block;
  background-color: #14c8eb;
  color: #fff;
  border: none;
  padding: 1.5em 1.6em;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
  width: 100%;
  position: absolute;
  bottom: 0;
  transition: background-color 0.5s ease-out;
  z-index: 1;
}

.art button:focus {
  outline: none;
}

.art button:hover {
  background-color: #0ea8c7;
}

.text-info {
  padding: 1rem 1.5rem;
  margin-bottom: 2.5rem;
}

.controls {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
  opacity: 0.5;
  z-index: 2;
}

.controls button {
  width: 40px;
  height: 40px;
  border: 0;
  background-color: #14c8eb;
  color: white;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.5s ease-out;
}

.controls button:hover {
  background-color: #0ea8c7;
}

.controls button:nth-child(1) {
  margin-right: 2rem;
}

.leftControl {
  position: absolute;
  top: 10%;
  left: 50%;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.rightControl {
  position: absolute;
  top: 10%;
  right: 50%;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

